// 引入vue
import Vue from 'vue'
// 引入渲染作用域
import App from './App'
// 引入路由模块
import router from './router'
// vue底层优化
Vue.config.productionTip = false
    // 引入按需模块
import { Button, Cell, NavBar, Tabbar, TabbarItem, Swipe, SwipeItem } from 'vant';

// ttp://localhost:5000
Vue
    .use(Button) // 按钮
    .use(Cell)
    .use(NavBar) // 导航栏
    .use(Tabbar) // tab切换
    .use(TabbarItem)
    .use(Swipe).use(SwipeItem); // 轮播图

// 设置全局的请求地址以 Vue.http.options.root = 'http://localhost:5000/';
Vue.http.options.root = 'http://localhost:5000/';
// 设置全局post请求的第三个参数
Vue.http.options.emulateJSON = true;



/* vue实例 */
new Vue({
    // 声明作用域
    el: '#app',
    // 挂载路由模块
    router,
    render: c => c(App)

})


