<template>
  <div class="hello">
    <!-- 轮播图 -->
    <van-swipe :autoplay="3000">
      <van-swipe-item v-for="(image, index) in images" :key="index">
        <img v-lazy="image">
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
export default {
  data() {
    return {
      autoplay:3000,
      images: [],
    }
  },
  methods:{
    
  }
};
</script>

<style>
