import Vue from 'vue'
import Router from 'vue-router'
// 会员页
import envelop from '../components/envelop.vue'
// 主页
import home from '../components/home.vue'
// 购物车
import shopping from '../components/shopping.vue'
// 搜索页
import search from '../components/search.vue'

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            redirect: '/home'

        },
        {
            path: '/envelop',
            component: envelop
        },
        {
            path: '/home',
            component: home
        },
        {
            path: '/shopping',
            component: shopping
        },
        {
            path: '/search',
            component: search
        }
    ]
})


